"use strict";
import * as React from 'react';
import Section from '../..//section/section';
import { Bookmarks } from '../../navigation/bookmark/bookmarks';
import { BookmarksEdit } from '../../navigation/bookmark/edit/bookmarksEdit';
import { data } from './bookmarkTestData';

export default class BookmarkTest extends React.Component<{
	bookmarks: Bookmarks,
	mainPageBookmark: any,
	profile: any,
	onClose: () => void

}, {}>{
	static representationName: string = "bookmark";
	render() {
		return <div>
			<Section useFlowLayout={true} title="Bookmark Link">
				<div className="s_profile_bar_bar">

				</div>
			</Section>
			<Section title="Bookmark Manage">
				<div>
					<BookmarksEdit bookmarks={this.props.bookmarks} profile={this.props.profile} onClose={this.props.onClose} />
				</div>
			</Section>
		</div>;
	}
}



