"use strict";
import * as React from 'react';
import * as Sdata from '../../../dataStore/sdata';

export class Diagnose extends React.Component<{
    diagnose: Sdata.IDiagnose
}, {}>{
    render() {
        return <span className={"s_field_diagnose s_has_" + this.props.diagnose.$severity}>{this.props.diagnose.$message}</span>;
    }
}


