"use strict";
import * as React from 'react';

export class Icon extends React.Component<{
    name:string,
    className?:string
},{}>{
    render(){
        return <i className={(this.props.className || "") + " s_sagearmonyeicon"}>{this.props.name}</i>;
    }
}


